<template>
<div class="range-demo demo">
    <div class="section">
      <h2 class="demo-title">range</h2> 
      <div class="box">
          <range-bar :params="{height: 1, value:10, max: 100}"></range-bar>
      </div>
      <div class="box">
          <range-bar :params="{height: 10, showValue: true, unit: '元', value:50, max: 100}"></range-bar>
      </div>
      <div class="box">
          <range-bar :params="{type: 'native', value:30, max: 100}"></range-bar>
      </div>
    </div>
</div>
</template>

<script type="text/javascript">
import Dialog from 'smui/Dialog'
import RangeBar from 'smui/RangeBar'

export default {
    data() {
        return {
            canBatch: false,
            params: {
                content: ''
            }
        }
    },
    components: {
        RangeBar
    }
}
</script>

<style type="text/css" lang="less">
.range-demo {
    .box {
        width: 100%;
        padding: 20px 50px;
    }

    .range-bar {
        width: 500px;
    }
}
</style>


