<template>
<div class="progress-demo demo">
    <div class="section">
      <h2 class="demo-title">Progress</h2> 
      <div class="box">
          <progress-bar :params="{height: 1, value:10, max: 100}"></progress-bar>
      </div>
      <div class="box">
          <progress-bar :params="{height: 10, showValue: true, unit: '元', value:50, max: 100}"></progress-bar>
      </div>
      <div class="box">
          <progress-bar :params="{type: 'native', value:30, max: 100}"></progress-bar>
      </div>
    </div>
</div>
</template>

<script type="text/javascript">
import Dialog from 'smui/Dialog'
import ProgressBar from 'smui/ProgressBar'

export default {
    data() {
        return {
            canBatch: false,
            params: {
                content: ''
            }
        }
    },
    components: {
        ProgressBar
    }
}
</script>

<style type="text/css" lang="less">
.progress-demo {
    .box {
        width: 100%;
    }
}
</style>


