<template>
<div class="range-bar">
    <div class="range-box" 
        @click="changeValue" v-if="type == 'custom'">
        <div class="min flag" :style="{left: 0}" v-if="showValue">
            <span >0{{unit}}</span>
        </div>
        <div class="bar" :style="{width: ratio, height: height+'px'}"></div>
        <div class="flag" :style="{left: ratio}" v-if="showValue">
            <span >{{value}} {{unit}}</span>
        </div>
        <div class="max flag" :style="{right: 0}" v-if="showValue">
            <span >{{max}} {{unit}}</span>
        </div>
    </div>
    <input type="range" v-model="value" max="max"
        @change="changeValue" v-show="type == 'native'"/>
</div>
</template>

<script type="text/javascript">
import ui from '../base/ui'

const params = {
    value: 0,
    height: 10,
    unit: '',
    showValue: false,
    type: 'custom',
    disabled: false,
    ratio: 0,
    min: 0,
    max: 100
}

export default {
    mixins: [ui],
    params: params,
    computed: {
        ratio() {
            return Math.floor(this.value * 100 / (this.max - this.min)) + '%'
        }
    },
    methods: {
        changeValue (e) {
            if (this.disabled) {
                return false
            }

            let el = this.$el.querySelector('.range-box')

            if (e && el) {
                this.value = (e.offsetX * this.max / el.clientWidth).toFixed(2)
            }
            this.emit('change-value', {value: this.value});
        }
    }
}
</script>

<style type="text/css" lang="less">
.range-bar {
    position: relative;
    .flag {
        width: 2px;
        height: 100%;
        margin-left: -1px;
        border-radius: 8px;
        background-color: orange;
        position: absolute;
        top: 0;
        span {
            display: inline-block;
            width: 100px;
            position: absolute;
            top: 1em;
            left: -1em;
        }
    }
    input[type=range] {
        width: 100%;
    }
    .bar {
        background-color: deepskyblue;
        height: 10px;
        border-radius: 5px; 
    }
    .range-box {
        border: 1px solid #EEE;
        border-radius: 5px;
        background-color: #EEE; 
        width: 100%;
        display: block;
        position: relative;
    }
}
</style>


